import Vue from 'vue';

/**
 * @description: 
 * @param {string} value - content
 * @param {string} num - contents size
 */
Vue.filter('lineMax', (value, num) => {
  return value.slice(0, num) + '...'
})

/**
 * @description: get momth or day.
 * @param {string} date - event's date
 * @param {string} arg1 - momth or day.
 */
Vue.filter('getTime', (date, arg1) => {
  let i = 1;
  if (arg1 === "month") {
    i = 1;
  } else if (arg1 === "day") {
    i = 2;
  }
  return date.split(" ")[i]
})

Vue.filter('getCn', (data) => {
  switch (data) {
    case 'share':
      return '';
      break;
    case 'ask':
      return '';
      break;
    case 'job':
      return '';
      break;
    case 'dev':
      return '';
      break;
    default:
      return 'error';
  }
})

/**
 * 
 *
 * @param {String} str
 * @returns 
 */
Vue.filter('formatDate', (str) => {
  if (!str) 
    return ''
  var date = new Date(str)
  var time = new Date().getTime() - date.getTime() //- =  = 
  if (time < 0) {
    return ''
  } else if ((time / 1000 < 30)) {
    return ''
  } else if (time / 1000 < 60) {
    return parseInt((time / 1000)) + ''
  } else if ((time / 60000) < 60) {
    return parseInt((time / 60000)) + ''
  } else if ((time / 3600000) < 24) {
    return parseInt(time / 3600000) + ''
  } else if ((time / 86400000) < 31) {
    return parseInt(time / 86400000) + ''
  } else if ((time / 2592000000) < 12) {
    return parseInt(time / 2592000000) + ''
  } else {
    return parseInt(time / 31536000000) + ''
  }
})


