import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import querystring from 'querystring'
Vue.use(VueAxios, axios)
    // jsdoc fetch -d docs axios  axios.defaults.timeout =
    // 5000;//,'timeout'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.baseURL = '/api';

//POST
axios
    .interceptors
    .request
    .use((config) => {
        config.data = querystring.stringify(config.data);
        return config;
    }, (error) => {
        _.toast("", 'fail');
        return Promise.reject(error);
    });

//
axios
    .interceptors
    .response
    .use((res) => {
        if (!res.data) {
            return Promise.reject(res);
        }
        return res;
    }, (error) => {
        _.toast("", 'fail');
        return Promise.reject(error);
    });

/**
 * @description   get 
 * @method fetch
 * @param {obj} params - .
 * @param {Number} page - 
 * @param {String} tab - , ask share job good
 * @param {Number} limit - 
 * @paramm {String} drender -  false  true markdown 
 */
function fetch(method, url, params) {
    if (method === 'get') {
        return new Promise((resolve, reject) => {
            axios
                .get(url + '?' + querystring.stringify(params))
                .then((response) => {
                    resolve(response.data);
                }, (err) => {
                    reject(err);
                })
                .catch((error) => {
                    reject(error)
                })
        })
    } else if (method === 'post') {
        console.log(querystring.stringify(params))
        return new Promise((resolve, reject) => {
            axios
                .post(url, querystring.stringify(params))
                .then((response) => {
                    console.log(response.data)
                    resolve(response.data);
                }, (err) => {
                    reject(err);
                })
                .catch((error) => {
                    reject(error)
                })
        })
    } else if (method === ':id') {
        return new Promise((resolve, reject) => {
            axios
                .get(url + '/' + params.id)
                .then((response) => {
                    console.log(response.data)
                    resolve(response.data);
                }, (err) => {
                    reject(err);
                })
                .catch((error) => {
                    reject(error)
                })
        })
    } else {
        console.log('fetch error');
    }
}

export default {
    // 
    /**
     * @description   get 
     * @method topics
     * @param {obj} params - .
     * @param {Number} page - 
     * @param {String} tab - , ask share job good
     * @param {Number} limit - 
     * @param {String} drender -  false  true markdown 
     */
    topics(params) {
        return fetch('get', '/topics', params)
    },

    /**
     * @description   get 
     * @method topics
     * @param {obj} params - .
     * @p* @param {String} mdrender -  false  true markdown 
     * @p* @param {String} accesstoken -  is_collect  replies  is_uped 
     */
    topicDetail(params) {
        return fetch(':id', '/topic', params)
    },

    /**
     * @description   post 
     * @method addTopic
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @param {String} tab -  dev 
     * @param {String} title  - 
     * @param {String} content - 
     * @example
        return {success: true, topic_id: '5433d5e4e737cbe96dcef312'}
     */
    addTopic(params) {
        return fetch('post', '/topics', params)
    },

    /**
     * @description   post 
     * @method updateTopic
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @param {String} topic_id - id
     * @param {String} tab -  dev 
     * @param {String} title  - 
     * @param {String} content - 
     * @example /api/v1/topic/5433d5e4e737cbe96dcef312
     */
    updateTopic(params) {
        return fetch('post', '/topics/update', params)
    },

    // 
    /**
     * @description   post 
     * @method collectTopic
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @param {String} topic_id - id
     * @example
        return {"success": true}
     */
    collectTopic(params) {
        return fetch('post', '/topic_collect/collect', params)
    },

    /**
     * @description   post 
     * @method deCollectTopic
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @param {String} topic_id - id
     * @example
        return {"success": true}
     */
    deCollectTopic(params) {
        return fetch('post', '/topic_collect/de_collect', params)
    },

    /**
     * @description   post 
     * @method gtAllCollectTopic
     * @example /api/v1/topic_collect/alsotang
     */
    gtAllCollectTopic(params) {
        return fetch('get', '/topic_collect/', params)
    },

    // 
    /**
     * @description   post 
     * @method addReplie
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @param {String} content  - 
     * @param {String} reply_id  - 
     * @example
        return {success: true, reply_id: '5433d5e4e737cbe96dcef312'}
     */
    addReplie(params) {
        return fetch('get', '/topic/:topic_id/replies', params)
    },

    /**
     * @description   post 
     * @method upsReplie
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @example
         action up or down
        return {"success": true, "action": "down"}
     */
    upsReplie(params) {
        return fetch('post', '/topic/:topic_id/replies', params)
    },

    //
    /**
     * @description   get 
     * @method userInfo
     * @example /api/v1/user/alsotang
     */
    userInfo(params) {
        return fetch('get', '/user/:loginname', params)
    },

    /**
     * @description   post 
     * @method checkToken
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @example
        return {success: true, loginname: req.user.loginname, id: req.user.id, avatar_url: req.user.avatar_url}
     */
    checkToken(params) {
        return fetch('post', '/topic/:topic_id/replies', params)
    },

    //
    /**
     * @description   get 
     * @method messageCount
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @example /api/v1/user/alsotang
     */
    messageCount(params) {
        return fetch('get', '/user/:loginname', params)
    },

    /**
     * @description   get 
     * @method messageCount
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @param {String} mdrender - String  false  true markdown 
     * @example
     * return {
            data: {
                has_read_messages: [],
                hasnot_read_messages: [
                {
                    id: "543fb7abae523bbc80412b26",
                    type: "at",
                    has_read: false,
                    author: {
                    loginname: "alsotang",
                    avatar_url: "https://avatars.githubusercontent.com/u/1147375?v=2"
                    },
                    topic: {
                    id: "542d6ecb9ecb3db94b2b3d0f",
                    title: "adfadfadfasdf",
                    last_reply_at: "2014-10-18T07:47:22.563Z"
                    },
                    reply: {
                    id: "543fb7abae523bbc80412b24",
                    content: "[@alsotang](/user/alsotang) ",
                    ups: [ ],
                    create_at: "2014-10-16T12:18:51.566Z"
                    }
                    },
                ...
                ]
            }
        }
     */
    messageCount(params) {
        return fetch('get', 'messages', params)
    },

    /**
     * @description   post 
     * @method checkToken
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @example
        return { success: true, marked_msgs: [ { id: '544ce385aeaeb5931556c6f9' } ] }
     */
    markAll(params) {
        return fetch('post', '/message/mark_all', params)
    },

    /**
     * @description   post 
     * @method markSignle
     * @param {obj} params - .
     * @param {String} accesstoken -  accessToken
     * @example
     *   return  { success: true,marked_msg_id: "58ec7d39da8344a81eee0c14" }
     */
    markSignle(params) {
        return fetch('post', '/message/mark_one/:msg_id', params)
    }
}


