<template>
    <el-row :gutter="20">
        <el-col :span="16">
            <el-card class="box-card" style="margin-top:20px;">
                <div class="grid-content">
                    <el-form ref="form" :model="form" label-width="80px">
                        <el-form-item label="">
                            <el-select v-model="form.region" placeholder="">
                                <el-option label="" value="share"></el-option>
                                <el-option label="" value="ask"></el-option>
                                <el-option label="" value="job"></el-option>
                                <el-option label="" value="dev"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="form.title"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input type="textarea" v-model="form.content"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" v-on:click="add()"></el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-card>
        </el-col>
        <side></side>
    </el-row>
</template>
<script>

import fetch from '../fetch'
import Side from '../components/Side'
export default {
    data() {
        return {
            form: {
                title: '',
                content: '',
                region: 'dev'
            }
        }
    },
    methods: {
        add() {
            fetch.updateTopic({ 
                "accesstoken": "74c3eaf8-ad2c-4931-b565-abc28702af3d", 
                "title": "22", 
                "tab": "dev", 
                "topic_id":'597b6ffa68aa87c774e5ed32',
                "content": "123232312222222222222222222222222" 
                })
                .then(function (res) {
                    console.log(res.data)
                }.bind(this))
                .catch(function (error) {
                    console.log("")
                    if (error.response) {
                        //2xx
                        console.log(error.response.data);
                        console.log(error.response.status);
                        console.log(error.response.header);
                    } else {
                        //
                        console.log('Error', error.message);
                    }
                    console.log(error.config);
                }.bind(this))
        },
    },
    props: [],
    computed: {
    },
    components: {
        Side
    },

    // 
    beforeCreate() {
        // 
    },
    created() {
    },
    beforeMount() {
        // DOM
    },
    mounted() {
        // DOM
    },
    beforeUpdate() {
        //  
    },
    updated() {
        //  
    },
    beforeDestroy() {
        // 
    },
    destory() {
        // 
    }
}
</script>

<style lang="sass" scoped>
</style>



