<template>
    <el-row :gutter="20">
        <el-col :span="4">
            <div class="grid-content bg-purple">
                <el-button type="primary" style="width:100%;" v-on:click="getDoubanData()"></el-button>
            </div>
        </el-col>
        <el-col :span="4">
            <div class="grid-content bg-purple">
                <el-button type="primary" style="width:100%;" v-on:click="getDoubanData('good')"></el-button>
            </div>
        </el-col>
        <el-col :span="4">
            <div class="grid-content bg-purple">
                <el-button type="primary" style="width:100%;" v-on:click="getDoubanData('share')"></el-button>
            </div>
        </el-col>
        <el-col :span="4">
            <div class="grid-content bg-purple">
                <el-button type="primary" style="width:100%;" v-on:click="getDoubanData('ask')"></el-button>
            </div>
        </el-col>
        <el-col :span="4">
            <div class="grid-content bg-purple">
                <el-button type="primary" style="width:100%;" v-on:click="getDoubanData('job')"></el-button>
            </div>
        </el-col>
        <el-col :span="4">
            <div class="grid-content bg-purple">
                <el-button type="primary" style="width:100%;"></el-button>
            </div>
        </el-col>
    </el-row>
</template>

<script>

export default {
    data() {
        return {
            msg: 'I am Nav.vue',
            tableData: []
        }
    },
    methods: {
        getDoubanData: function (tab) {
            this.axios.get('/v1/topics?tab' + tab + '&page=5&mdrender=false&limit=10')
                .then(function (res) {
                    console.log(res.data);
                    this.tableData = res.data.data
                }.bind(this))
                .catch(function (error) {
                    console.log("")
                    if (error.response) {
                        //2xx
                        console.log(error.response.data);
                        console.log(error.response.status);
                        console.log(error.response.header);
                    } else {
                        //
                        console.log('Error', error.message);
                    }
                    console.log(error.config);
                }.bind(this));
        }
    },
    props: [],
    computed: {
    },
    components: {
    },

    // 
    beforeCreate() {
        // 
    },
    created() {
    },
    beforeMount() {
        // DOM
    },
    mounted() {
        // DOM
    },
    beforeUpdate() {
        //  
    },
    updated() {
        //  
    },
    beforeDestroy() {
        // 
    },
    destory() {
        // 
    }
}
</script>

<style lang="sass" scoped>
.el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>



