<template>
    <el-row :gutter="20" v-if="detailData">
        <el-col :span="16">
            <el-card class="box-card" style="margin-top:20px;">
                <div slot="header" class="clearfix">
                    <el-tag type="primary" v-if="detailData.top"></el-tag>
                    <el-tag type="primary" v-else-if="detailData.good"></el-tag>
                    <el-tag type="primary" v-else>{{detailData.tab | getCn}}</el-tag>
                    <span style="line-height: 36px;">{{ detailData.title}}</span>
                    <div>
                         {{ detailData.create_at.split("T")[0] }}  {{ detailData.author.loginname }} {{ detailData.author.visit_count }}   {{ detailData.last_reply_at }} {{ detailData.author.visit_count }} 
                        <el-tag type="primary">{{ detailData.tab | getCn}}</el-tag>
                    </div>
                </div>
                <div class="grid-content">
                    <section class='markdown-body topic-content' v-html="detailData.content"></section>
                </div>
            </el-card>
            <el-card class="box-card" style="margin-top:20px;">
                <div class="grid-content" v-for="(replie,index) in detailData.replies" :key="replie.id">
                    <div>{{index}} {{ replie.author.loginname }}</div>
                    <div>
                        <img :src="replie.author.avatar_url" style="idth: 30px;height: 30px;border-radius: 3px;">
                    </div>
                    <div v-html="replie.content" class="content"></div>
                    <div style="width:100%;height:1px;background:#ddd;"></div>
                </div>
            </el-card>
        </el-col>
        <el-col :span="8">
            <el-card class="box-card" style="margin-top:20px;min-height:700px;">
                <div>
                    <p>CNodeNode.js123</p>
                    <p>    , </p>
                    <el-button type="primary">github</el-button>
                </div>
            </el-card>
            <el-card class="box-card" style="margin-top:20px;">
                <div>
                    <img src="~assets/images/banner1.png" style="max-width:100%;">
                    <img src="~assets/images/banner2.png" style="max-width:100%;">
                    <img src="~assets/images/banner3.png" style="max-width:100%;">
                    <img src="~assets/images/banner4.png" style="max-width:100%;">
                </div>
            </el-card>
            <el-card class="box-card" style="margin-top:20px;">
                <div>
                    <img src="~assets/images/golangtc-logo.png" style="max-width:70%;">
                    <img src="~assets/images/TesterHome-logo.png" style="max-width:70%;">
                    <img src="~assets/images/phphub-logo.png" style="max-width:70%;">
                    <img src="~assets/images/ruby-logo.png" style="max-width:70%;">
                </div>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
import fetch from '../fetch' // axios
export default {
    data() {
        return {
            detailData: undefined
        };
    },

    methods: {

    }
    , props: [],
    computed: {
    },
    components: {
    },

    // 
    beforeCreate() {
        // 
    },
    created() {
        fetch.topicDetail({
            id: this.$route.query.id
        }).then(function (res) {
            this.detailData = res.data;
        }.bind(this))
            .catch(function (error) {
                console.log("")
                if (error.response) {
                    //2xx
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.header);
                } else {
                    //
                    console.log('Error', error.message);
                }
                console.log(error.config);
            }.bind(this));
    },
    beforeMount() {
        // DOM
    },
    mounted() {
        // DOM
    },
    beforeUpdate() {
        //  
    },
    updated() {
        //  
    },
    beforeDestroy() {
        // 
    },
    destory() {
        // 
    }
}
</script>

<style lang="sass" scoped>
.el-row {
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
}

.el-col {
    //   
    border-radius: 7px;
}

.bg-purple-dark {
    background: #99a9bf;
}

.bg-purple {
    background: #d3dce6;
}

.bg-purple-light {
    background: #e5e9f2;
}

.grid-content {
    border-radius: 4px;
    min-height: 36px;
}

.row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
}
</style>



